.Loader {
  width: 150px;
  height: 150px;
  margin: 5rem auto;
  border-radius: 50%;
  background: -webkit-linear-gradient(
    45deg,
    transparent,
    transparent 40%,
    rgb(0, 222, 255)
  );
  background: -o-linear-gradient(
    45deg,
    transparent,
    transparent 40%,
    rgb(0, 222, 255)
  );
  background: linear-gradient(
    45deg,
    transparent,
    transparent 40%,
    rgb(0, 222, 255)
  );
  -webkit-animation: animate 2s linear infinite;
          animation: animate 2s linear infinite;
}

@-webkit-keyframes animate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes animate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Loader:before {
  content: "";
  position: absolute;
  top: 6px;
  left: 6px;
  right: 6px;
  bottom: 6px;
  background: var(--dark);
  border-radius: 50%;
  z-index: 10;
}

.Loader:after {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background: -webkit-linear-gradient(
    45deg,
    transparent,
    transparent 40%,
    rgb(0, 222, 255)
  );
  background: -o-linear-gradient(
    45deg,
    transparent,
    transparent 40%,
    rgb(0, 222, 255)
  );
  background: linear-gradient(
    45deg,
    transparent,
    transparent 40%,
    rgb(0, 222, 255)
  );
  border-radius: 50%;
  z-index: 1;
  -webkit-filter: blur(30px);
          filter: blur(30px);
}



