.Navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  height: 60px;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 0 1.8rem;
  z-index: 200;
  background: var(--dark);
}

.Text {
  text-decoration: none;
  font-size: 3rem;
  color: var(--light);
  font-family: "Orbitron", sans-serif;
  cursor: pointer;
  -webkit-transition: var(--transtion);
  -o-transition: var(--transtion);
  transition: var(--transtion);
}

.Text:hover {
  text-shadow: 0 0 2px rgba(202, 228, 225, 0.92),
    0 0 10px rgba(202, 228, 225, 0.34), 0 0 10px rgba(30, 132, 242, 0.52),
    0 0 10px rgba(30, 132, 242, 0.92), 0 0 10px rgba(30, 132, 242, 0.78),
    0 0 10px rgba(30, 132, 242, 0.92);
}

@media (max-width: 749px) {
  .Text {
    font-size: var(--bg-font);
  }
  .DesktopOnly {
    display: none;
  }
}
@media (max-width: 420px) {
  .Navbar {
    padding: 0;
  }
  .Text {
    margin-left: 2rem;
  }
}



